@import '../_includes';

body {
  display: flex;
  flex-direction: column; }

.heading-h2 {
  font-weight: bold;
  font-size: 50px;
  line-height: 60px;
  text-align: center;
  color: $color-dark-charcoal; }

.section-description {
  text-align: center;
  max-width: 790px;
  margin: 0 auto; }

.slider {
  max-width: 920px;
  margin: 0 auto;
  &-for {
    &__img {
      object-fit: cover;
      object-position: center;
      max-width: unset;
      width: 100%;
      height: inherit; } }
  &-nav {
    &__img {
      object-fit: cover;
      object-position: center;
      max-width: unset;
      width: 100%;
      height: inherit; } } }

#lightgallery {
  display: flex;
  flex-wrap: wrap;
  .lg-item {
    width: 25%;
    height: 290px;
    overflow: hidden;
    position: relative; }
  .lg-preview {
    object-fit: cover;
    object-position: center;
    max-width: unset;
    width: 100%;
    height: inherit; }
  .lg-poster {
    background-color: rgba(0, 0, 0, 0.1);
    bottom: 0;
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
    -webkit-transition: background-color 0.15s ease 0s;
    -o-transition: background-color 0.15s ease 0s;
    transition: background-color 0.15s ease 0s; }
  .lg-icon {
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    opacity: 0;
    position: absolute;
    top: 50%;
    -webkit-transition: opacity 0.3s ease 0s;
    -o-transition: opacity 0.3s ease 0s;
    transition: opacity 0.3s ease 0s;
    border-radius: 50%;
    height: 64px;
    width: 64px;
    background: $color-malachite;
    display: flex;
    justify-content: center;
    align-items: center; }

  .lg-item:hover .lg-poster {
    background-color: rgba(52, 60, 74, 0.6); }

  .lg-item:hover .lg-poster > .lg-icon {
    opacity: 1; } }
